*{
    margin: 0;
    padding: 0;
}
body {
    font-family: "微软雅黑"
}
ul {
    padding-left: 0;
    list-style: none
}
a {
    color: #7d7d7d;
    text-decoration: none
}
a:hover, a:focus, a:active {
    text-decoration: none
}
.main_visual{
    width: 100%;
}
.main_image {
    width: 100%;
    height: 445px;
    overflow: hidden;
    margin: 0 auto;
    position: relative
}
.main_image ul {
    width: 9999px;
    height: 445px;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0
}
.main_image li {
    float: left;
    width: 100%;
    height: 445px
}
.main_image li span {
    display: block;
    width: 100%;
    height: 445px
}
.main_image li a {
    display: block;
    width: 100%;
    height: 445px
}
.main_image li .img_1 {
    background: url('../imgs/banner/1.jpg') center top no-repeat
}
.main_image li .img_2 {
    background: url('../imgs/banner/2.jpg') center top no-repeat
}
.main_image li .img_3 {
    background: url('../imgs/banner/3.jpg') center top no-repeat
}
.main_image li .img_4 {
    background: url('../imgs/banner/1.jpg') center top no-repeat
}
div.flicking_con {
    width: 990px;
    margin: 0 auto;
    position: relative
}
div.flicking_con .flicking_inner {
    position: absolute;
    top: 405px;
    left: 50%;
    z-index: 999;
    width: 300px;
    height: 21px
}
div.flicking_con a {
    float: left;
    width: 21px;
    height: 21px;
    padding: 0;
    margin-right: 3px;
    background: url('../imgs/banner/btn_off.png') 0 0 no-repeat;
    display: block;
    text-indent: -1000px
}
div.flicking_con a.on {
    background: url('../imgs/banner/btn_on.png') 0 0 no-repeat
}
div.flicking_con a:last-child {
    margin-right: 0
}
#btn_prev, #btn_next {
    z-index: 11111;
    position: absolute;
    display: block;
    width: 35px !important;
    height: 56px !important;
    top: 50%;
    margin-top: -37px
}
#btn_prev {
    background: rgba(0, 0, 0, .25) url(../imgs/banner/prev.png) no-repeat center center;
    left: 100px;
}
#btn_next {
    background: rgba(0, 0, 0, .25) url(../imgs/banner/next.png) no-repeat center center;
    right: 100px;
}